@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

/* variables */
:root {
	--heading-color: #444;
	--text-color: #999;
	--primary-color: #8d69f1;
	--highlight-color: #d13267;
	--bg-color: #f4f4f4;
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

/* base styles */
body {
	font-family: Poppins, sans-serif;
	margin: 0;
	font-size: 1.1em;
	background: var(--bg-color);
	overflow: hidden;
}
ul {
	list-style-type: none;
}

/* layout */
.page-title {
	font-size: 1em;
	color: var(--heading-color);
	display: inline-block;
}
.btn {
	background: #fff;
	padding: 8px 12px;
	border-radius: 4px;
	color: var(--primary-color);
	cursor: pointer;
	font-size: 1em;
	border: 1px solid var(--primary-color);
}
.btn:hover {
	color: #fff;
	background-color: var(--primary-color);
}

/* forms */
label {
	display: block;
	margin: 24px auto;
}
label span {
	display: block;
	margin-bottom: 6px;
}
input,
textarea {
	padding: 8px 6px;
	font-size: 1em;
	color: #777;
	width: 100%;
	box-sizing: border-box;
	border: 1px solid #ddd;
	border-radius: 4px;
}
textarea {
	min-height: 160px;
}
.error {
	color: red;
	background: pink;
	border: 1px solid red;
	border-radius: 4px;
	padding: 8px;
	margin: 10px 0;
}

.active {
	background-color: #fff;
	background: var(--bg-color);
	border-radius: 20px 0 0 20px;
}

.active span {
	color: #555;
}

.active img {
	filter: invert(40%) !important;
}

@media (max-width: 600px) {
	body {
		overflow: scroll;
	}
}
